import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

const _ = require('lodash');
const rws = require('robust-websocket');

class App extends Component {
  
  constructor(props) {
    super(props);
    
    this.state = {
      count: 90
    }
    
    this.values = []
  }
  
  componentDidMount() {
    this.initSocket()
  }
  
  initSocket() {
    const ws = 
    new rws('wss://mobileroad.bacc6666.com/', null, {
    shouldReconnect: (event, wss) => {
      return Math.pow(1.5, ws.attempts) * 500
    }
  });
  
  ws.addEventListener('open', this.openedSocket);
  
  ws.addEventListener('message', this.receivedMessage);
  
  ws.addEventListener('close', this.closedSocket);
}

openedSocket = (event) => {
  console.log('__OPEN__')
}

receivedMessage = (event) => {
  //console.log(event.data)
  let parseJson = JSON.parse(event.data)
  //console.log(parseJson)
  this.values.push(parseJson)
}

closedSocket = (event) => {
  console.log('__CLOSE__')
  let onlyBacc = _.filter(this.values, (data) => {
    return data.m_now.tp === "Bacc"
  });

  console.log(onlyBacc);
  this.values = []
}

render() {
  return (
    <div className= "container-fluid">
    
    <div className="App">

    <header className="App-header">
    <img src={logo} className="App-logo" alt="logo" />
    <h1 className="App-title">Welcome to React</h1>
    </header>
    
    <div className="card-deck">

    <div className="card">
    <h4 className="card-header">Featured</h4>
    <div className="card-body">
    <h4 className="card-title">Special title treatment</h4>
    <p className="card-text">With supporting text below as a natural lead-in to additional content.</p>
    <a className="btn btn-primary">Go somewhere</a>
    </div>
    </div>

    <div className="card">
    <h4 className="card-header">Featured</h4>
    <div className="card-body">
    <h4 className="card-title">Special title treatment</h4>
    <p className="card-text">With supporting text below as a natural lead-in to additional content.</p>
    <a className="btn btn-primary">Go somewhere</a>
    </div>
    </div>
    
    <div className="card">
    <h4 className="card-header">Featured</h4>
    <div className="card-body">
    <h4 className="card-title">Special title treatment</h4>
    <p className="card-text">With supporting text below as a natural lead-in to additional content.</p>
    <a className="btn btn-primary">Go somewhere</a>
    </div>
    </div>

    </div>

    </div>
    </div>
  );
}
}

export default App;



